//====================================================================================================
.main {

  &__container {}

  &__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    row-gap: rem(15);
    column-gap: rem(17);
		@media (max-width:$mobileSmall){
			grid-template-columns: 1fr;
			grid-template-rows: 1fr 1fr 1fr;
		}
  }

  &__input {
    height: rem(35);
    border: rem(1) solid #D5D4D8;
    border-radius: rem(5);
    padding: 0 rem(10);
    transition: border 0.3s ease 0s;

    &:focus {
      border: rem(1) solid #9d7df5;
    }
  }

  &__button {
    // height: rem(40);
    grid-area: 2 / span 2;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    border-radius: rem(5);
    font-weight: 700;
    font-size: rem(16);
    line-height: math.div(19, 16);
    letter-spacing: -0.1em;
    color: #FFFFFF;
    transition: border 0.3s ease 0s;
    border: rem(2) solid transparent;
		@media (max-width:$mobileSmall){
			grid-area: auto;
		}

    @media (any-hover: hover) {
      &:hover {
        border: rem(2) solid #002fff;
      }
    }
  }

  &__image {
    @include adaptiveValue("margin-top", 36, 15);
    text-align: center;
		position: relative;

		&h2, :last-child{
			bottom: 0;
			top: auto;
		}

    & img {
      max-width: 100%;
      border: rem(4) dashed black
    }

		&:last-child{
			bottom: 0;
		}

    &_text {
      position: absolute;
      width: 80%;
      text-align: center;
      left: 50%;
      transform: translateX(-50%);
      margin: rem(15) 0;
      padding: 0 rem(5);
      font-family: impact, sans-serif;
      font-size: 2em;
      text-transform: uppercase;
      color: white;
      letter-spacing: rem(1);
			top: 0;
      text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
    }
  }
}

