//====================================================================================================
.header {
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);

  &__container {
    display: flex;
    align-items: center;
		gap: rem(7);
		@include adaptiveValue("padding-top", 19, 8);
		@include adaptiveValue("padding-bottom", 19, 8);
  }

  &__image {
		max-width: rem(32);
    img {
      max-width: 100%;
    }
  }

  &__title {
		font-weight: 700;
		font-size: rem(21);
		line-height: math.div(24, 21);
		letter-spacing: -0.1em;
		flex: 1 1;
	}

  &__text {
		font-family: "impact";
		font-size: rem(11);
	}
}

//====================================================================================================